.chat{
    flex-grow: 1;
    overflow-y: auto
    
} 
.chat > li{
    list-style-type: none;
    padding: 0.5em 0.5em 0 0.5em;
    margin: 1em;
    border: 1px solid var(--main-color);
    border-radius: 10px;
}
.timestamp {
    margin: 0;
    color: grey;
    font-size: small;
}
.own {
    background-color: rgb(190, 226, 190);
}
.unknown{
    background-color: white;
}